<script>
import { mapGetters, useStore } from "vuex";
import { useI18n } from "vue-i18n";
import { useRouter } from "vue-router";
import { computed, ref } from "vue";
// import i18n from "../../locales";
import { addressShorter } from "../../utils/address_util";
export default {
  setup() {
    const state = useStore()
    const route = useRouter();
    const i18n = useI18n();
    const toHome = () => {
      route.push("/");
    };

    const changeLang = () => {
      // this.$i18n.locale = this.$i18n.locale === "en" ? "zh" : "en";
      i18n.locale.value = i18n.locale.value === "zh" ? "en" : "zh";
      localStorage.setItem("lang", i18n.locale.value);
    };

    const startLinkWallet = async () => {
      await state.dispatch('deposit/initDeposit')
      // await state.dispatch('web3s/init')
    }

    return {
      toHome,
      changeLang,
      startLinkWallet
    };
  },
  methods: {
    // changeLang() {
    //   console.log('====================================');
