    //   localStorage.setItem("lang", this.$i18n.locale);
    // },
  },
  computed: {
    ...mapGetters([
      "isMintable",
      "addressOfEth",
      "contractsAted",
      "connectStatusId",
      "totalSupply",
      "maxSupply",
    ]),
    walletAddress() {
      // console.log(addressShorter, this.deposit, '123123');
      return addressShorter(this.addressOfEth);
    },
  },
  watch: {
    connectStatusId: function (newV, oldV) {
      this.$log.info("newV=", newV, "oldV=", oldV);
      // if (newV == 5) {
