import Web3 from "web3";
import { ethers } from "ethers";
import { getImgData } from "../../utils";
import { ElMessage } from "element-plus";
import i18n from "../../locales";

let _tokenIds = [];
const state = {
  account: null,
  web3: null,
  chainId: null,
  // 链ID
  // 4是测试RINkeBY
  networkSpec: process.env.NODE_ENV === 'development' ? 4 : 1,
  // 1是以太坊主网
  // networkSpec: 1,
  balanceOfEth: 0,

  // 测试合约地址
  usdt: "0x8E22A20F769467383331807821D30d1F78AD2419",
  wcamin: "0x55c045ACa20FB9151809550C562Ca131DF44Af6D",
  nft: "0x07D5F53733506E68bE28fe36DDA2816760AB9417",
  // "0x7b1a0dfe44f802b4c14b4ef5f09d124fa20452e1cbc34ae8076ae5cbe085bdf8"
  // 正式合约地址
  // usdt: "",
  // wcamin: "",
  // nft: "",
  // nft列表
  NFTlist: [],
  // 加载NFT进度条
  loadNFT: 0,
  // usdt
  usdtDepositContract: null,
  // 质押
  wcaminDepositContract: null,
  // nft 地址
  nftDepositContract: null,

  // // state: get from chain
  // gasPrice: 1e9,

  // fell:flags
  contractsAted: false,
  isMintable: false,
  isClaimable: false
};
const mutations = {
  SET_WEB3: (state, web3) => {
    state.web3 = web3;
  },
  SET_DEPOSIT_ACCOUNT: (state, account) => {
    state.account = account;
  },
  SET_CHAIN_ID: (state, chainId) => {
    state.chainId = chainId;
  },
  SET_NETWORK_SPEC: (state, networkSpec) => {
    state.networkSpec = networkSpec;
  },
  SET_BALANCE_OF_ETH: (state, b) => {
    state.balanceOfEth = b;
  },

  // usdt 合约
  SET_USDT_CONTRACT: (state, t) => {
    state.usdtDepositContract = t;
  },
  // 质押
  SET_WCAMIN_CONTRACT: (state, nft) => {
    state.wcaminDepositContract = nft;
    // console.log("nft1", nft);
  },
  // nft 合约
  SET_NFT_CONTRACT: (state, u) => {
    state.nftDepositContract = u;
  },

  SET_GAS_PRICE: (state, p) => {
    state.gasPrice = p;
  },
  SET_CONTRACT_ATED: (state, at) => {
    state.contractsAted = at;
  },
  SET_IS_MINTABLE: (state, m) => {
    state.isMintable = m;
  },
  SET_IS_CLAIMABLE: (state, c) => {
    state.isClaimable = c;
  },
  SET_NFTLIST(state, list) {
    state.NFTlist = list;
  },
  SET_LOADING_NFT(state, load) {
    state.loadNFT = load;
  },
};
const actions = {
  
  async initDeposit({ state, commit, dispatch }) {
    if (window.ethereum) {
      const web3 = new Web3(window.ethereum);
      console.log(1);
      commit("SET_WEB3", web3)

      // 监听链ID和账号变更
      dispatch("onChainChanged")
      dispatch("onAccountsChanged")
      dispatch("onDisconnect")
      dispatch("onConnect")
      dispatch("connect")
    } else {
      commit("SET_CONTRACT_ATED", false)
      global.$log.error("need install metamask");
      return Promise.reject("need install metamask");
    }
  },
  // 初始化
  async initDepositWeb3({ state, dispatch, commit }) {
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner(0);

    // 质押合约
    let wcmainContract = new ethers.Contract(
      state.wcamin,
      require("../../contracts/deposit/WcaMining.json"),
      signer
    );
    commit("SET_WCAMIN_CONTRACT", wcmainContract);

    // 代币合约
    let usdtDepositContract = new ethers.Contract(
      state.usdt,
      require("../../contracts/deposit/Usdt.json"),
      signer
    );
    commit("SET_USDT_CONTRACT", usdtDepositContract);

    // nft合约
    let nftDepositContract = new ethers.Contract(
      state.nft,
      require("../../contracts/deposit/WcaNft.json"),
      signer
    );
    commit("SET_NFT_CONTRACT", nftDepositContract);
    
    if(state.NFTlist.length === 0) {
      if(state.chainId === state.networkSpec)  {
        console.log('获取NFT');
        await dispatch("getNFTlist");
      } else {
        commit("SET_LOADING_NFT", -1)
        // "Please Switch Network To Ethereum Mainnet"
      ElMessage.warning(i18n.global.t('changeNetwork'))
      }
    }
  },
  // mint NFT
  async mintNFT({ state, dispatch }, total) {
    if(state.chainId  !== state.networkSpec) {
      // "Please Switch Network To Ethereum Mainnet"
      ElMessage.warning(i18n.global.t('changeNetwork'))
      return;
    }
    try {
      const balance = await state.usdtDepositContract.allowance(
        state.account,
        state.nft
      );
      if (balance < 10000) {
        console.log("授权一下！！！");
        let nftContce = await state.usdtDepositContract.approve(
          state.nft,
          ethers.utils.parseEther("100000000")
        );
        await nftContce.wait();
      }
      let balanceRes = await state.nftDepositContract.mintByUser(total);
      await balanceRes.wait();
      dispatch("getNFTlist")
      return true;
    } catch (error) {
      console.log(error, "mintNFT error");
      if(error.message.indexOf('cannot estimate gas; transaction may fail or may require manual gas limit') !== -1) {
        // 余额不足
        ElMessage.warning(i18n.global.t('nofunds'))
      } else if (error.code === 4001 || error.message) {
        ElMessage.warning(error.message)
      } else {
        alert(error.message || error);
      }
    }
  },
  
  // 获取nft
  async getNFTlist({ state, commit }) {
    _tokenIds = []
    const contract = state.nftDepositContract
    let index = 0;
    const getData = async (tokenId, depositeds, total) => {
        index += 1;
        const tokenURI = await contract.tokenURI(tokenId);
        // let hadDepositeds = false;
        // if (checkDeposit) {
        //   hadDepositeds = await state.wcaminDepositContract.hadDepositeds(
        //     tokenId
        //   );
        // }
        let { meta_Data, requestImgUrl } = await getImgData(
          tokenURI,
          tokenId,
          state.nft
        );
        const resultData = {
          ...meta_Data,
          requestImgUrl,
          tokenId,
          depositeds,
          nftBalance: total,
        };
        _tokenIds.push(resultData);
        // if (tokenId == 3 || tokenId == 4 || tokenId == 5) {
        //   console.log(resultData, " resut");
        // }
        commit("SET_NFTLIST", [..._tokenIds]);
        commit(
          "SET_LOADING_NFT",
          parseInt(((index + 1) / total) * 100)
        );
    };

    const depositRecords = await state.wcaminDepositContract.getDepositedNfts();
    const newDepositRecords = depositRecords.filter(record => !record.hadWithdraw) 
    console.log(newDepositRecords, 'depositRecords');
    
    const nftBalance = await contract.balanceOf(state.account);
    const total = nftBalance.toNumber() + newDepositRecords.length;
    console.log(total,'total');
    for (let i = 0; i < newDepositRecords.length; i++) {
      await getData(newDepositRecords[i].tokenId, newDepositRecords[i], total)
    }
    for (let i = 0; i < nftBalance.toNumber(); i++) {
      const tokenId = await contract.tokenOfOwnerByIndex(state.account, i);
      await getData(tokenId, {}, total)
    }

    commit("SET_LOADING_NFT", 101);
    _tokenIds = []
  },
  // 质押
  async startDeposit({ state, dispatch, commit }, tokenId) {
    if(state.chainId  !== state.networkSpec) {
      // "Please Switch Network To Ethereum Mainnet"
      ElMessage.warning(i18n.global.t('changeNetwork'))
      return;
    }
    try {
      const isApproved = await state.nftDepositContract.isApprovedForAll(
        state.account,
        state.wcamin
      );
      // 授权
      if (!isApproved) {
        const hadApproved = await state.nftDepositContract.setApprovalForAll(
          state.wcamin,
          true
        );
        await hadApproved.wait();
      }
      const resWait = await state.wcaminDepositContract.deposit(tokenId);
      const res = await resWait.wait();
      console.log(resWait, res, "res");
      await dispatch('depositAfter', tokenId)
      return res;
    } catch (error) {
      console.log(error, "Deposit error");
      if (error.code === 4001 || error.message) {
        ElMessage.warning(error.message)
      } else {
        alert(error.message || error);
      }
    }
  },
  // 操作之后执行数据转换
  async depositAfter({ state, commit }, tokenId) {
    const NFTIndex = state.NFTlist.findIndex(nft => nft.tokenId.toString() === tokenId.toString());
    // console.log(NFTIndex, 'NFTIndex');
    // 找到需要操作的那一条数据
    const changeNFT = state.NFTlist[NFTIndex];
    // console.log(changeNFT, changeNFT.tokenId, 'changeNFT');
    // 获取对应的质押数据
    const depositRecords = await state.wcaminDepositContract.getDepositedNfts();
    const newDepositRecords = depositRecords.filter(record => !record.hadWithdraw)
    // console.log(newDepositRecords, 'newDepositRecords');
    const depositeds = newDepositRecords.find(record => record.tokenId.toString() === changeNFT.tokenId.toString());
    // console.log(depositeds, 'depositeds');
    // 重新组装数据
    const newNFTItem = {
      ...changeNFT,
      depositeds: depositeds || {},
    }
    // 改变数据插进去
    const newNFTList = [...state.NFTlist];
    newNFTList[NFTIndex] = newNFTItem;
    _tokenIds[_tokenIds.findIndex(nft => nft.tokenId.toString() === tokenId.toString())] = newNFTItem;
    commit("SET_NFTLIST", newNFTList)
  },
  // 一键领取
  async claim({ state }, id) {
    if(state.chainId  !== state.networkSpec) {
      // "Please Switch Network To Ethereum Mainnet"
      ElMessage.warning(i18n.global.t('changeNetwork'))
      return;
    }
    try {
      if (id) {
        //  单个提取，传入质押ID
        const res = await (
          await state.wcaminDepositContract.claimByDepositRecordId(id)
        ).wait();
        console.log(res, id, "单个提取 res");
      } else {
        // do claim 全部提取
        const res = await (await state.wcaminDepositContract.claimAll()).wait();
        console.log(res, "提取 res all");
      }
// ?Success
      ElMessage(i18n.global.t('claimSuccess'));
    } catch (error) {
      console.log(error, "claim error");
      if (error.code === 4001 || error.message) {
        ElMessage.warning(error.message)
      } else {
        alert(error.message || error);
      }
    }
  },
  // 一键赎回
  async withdraw({ state, dispatch }, id) {
    if(state.chainId  !== state.networkSpec) {
      // "Please Switch Network To Ethereum Mainnet"
      ElMessage.warning(i18n.global.t('changeNetwork'))
      return;
    }
    try {
      if (id) {
        // 单个赎回，传入质押ID
        const res = await (
          await state.wcaminDepositContract.withdrawByDepositRecordId(id)
        ).wait();
        dispatch('withdrawAfter', id)
        console.log(res, id, '单个赎回 res');
      } else {
        // do withdraw 全部赎回
        const res = await (
          await state.wcaminDepositContract.withdrawAll()
        ).wait();
        console.log(res, "赎回 res all");
        dispatch('withdrawAfterAll', id)
      }

      // ElMessage("Success");
      // Success
      ElMessage(i18n.global.t('withdrawSuccess'));
    } catch (error) {
      console.log(error, "withdraw error");
      if (error.code === 4001 || error.message) {
        ElMessage.warning(error.message)
      } else {
        alert(error.message || error);
      }
    }
  },
  withdrawAfterAll({ state, dispatch }, id) {
    const NFTWithdrawList = state.NFTlist.filter(nft => nft.depositeds?.id?.toString());
    for (let index = 0; index < NFTWithdrawList.length; index++) {
      const NFTWithdraw = NFTWithdrawList[index];
      dispatch('withdrawAfter', NFTWithdraw.depositeds.id)
    }
  },
  withdrawAfter({ state, commit }, id) {
    const NFTIndex = state.NFTlist.findIndex(nft => nft.depositeds?.id?.toString() === id.toString());
    // console.log(NFTIndex, 'NFTIndex');
    // 找到需要操作的那一条数据
    const changeNFT = state.NFTlist[NFTIndex];
    // 重新组装数据
    const newNFTItem = {
      ...changeNFT,
      depositeds: {},
    }
    // 改变数据插进去
    const newNFTList = [...state.NFTlist];
    newNFTList[NFTIndex] = newNFTItem;
    _tokenIds[_tokenIds.findIndex(nft => nft.depositeds?.id?.toString() === id.toString())] = newNFTItem;
    commit("SET_NFTLIST", newNFTList)
  },
  async onChainChanged ({ dispatch,commit }) {
    window.ethereum.on("chainChanged", async function (_chainId) {
      console.log(+BigInt(_chainId).toString(), state.networkSpec, 'chainId change');
      commit("SET_CHAIN_ID", +BigInt(_chainId).toString())
      if(+BigInt(_chainId).toString() === state.networkSpec) {
        dispatch("initDepositWeb3");
      } else {
        // "Please Switch Network To Ethereum Mainnet"
        commit("SET_LOADING_NFT", -1)
        ElMessage.warning(i18n.global.t('changeNetwork'))
      }
    });
  },
  async onAccountsChanged ({ commit, dispatch }) {
    console.log('监听账号变更');
    window.ethereum.on("accountsChanged", async function (accounts) {
      if(accounts[0]) {
        console.log(accounts[0], 'accounts change');
        commit("SET_DEPOSIT_ACCOUNT", accounts[0]);
      } else {
        commit("SET_DEPOSIT_ACCOUNT", null)
      }
    });
  },
  async onDisconnect () {
    window.ethereum.on("disconnect", async function (error) {
      console.log("metamask event : onDisconnect error=", error);
      // TODO: may be need clear other state
      console.log('断开链接', error);
      // await dispatch("connect");
    });
  },
  async onConnect ({ state, dispatch }) {
    window.ethereum.on("connect", async function (connectInfo) {
      console.log(connectInfo, 'connectInfo');
      // ignore : connect action had handle
      if(state.contractsAted) {
        await dispatch("initDepositWeb3");
      }
    });
  },
  async connect ({ state, dispatch, commit }) {
    try {
      await window.ethereum.enable();
      console.log("finish wallet connect");
      // 获取账号和链
      const account = await state.web3.eth.getCoinbase();
      const chainId = await state.web3.eth.getChainId();
      // 设置账号链
      commit("SET_CHAIN_ID", chainId)
      commit("SET_DEPOSIT_ACCOUNT", account);
      commit("SET_CONTRACT_ATED", true)
      await dispatch("initDepositWeb3");
      return Promise.resolve();
    } catch (err) {
      commit("SET_CONTRACT_ATED", false)
      return Promise.reject(err);
    }
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
