import axios from "axios"

const agentHost = "https://freedom-proxy-nft-metainfo.vercel.app";

axios.interceptors.response.use((response) => {
    return response.data;
})

axios.interceptors.request.use((request) => {
    return request;
})

const request = {
    get: (url, param) => axios.get(url, param),
    post: (url, param) => axios.post(url, param),
    agentNFT: (url, param) => axios.get(`${agentHost}/api/nft?turi=${encodeURI(url)}`, param),
    agentDOM: (url, param) => axios.get(`${agentHost}/api/dom?turi=${encodeURI(url)}`, param),
    agentImg: (url, param) => axios.get(`${agentHost}/api/img?turi=${encodeURI(url)}`, param),
}

export default request;